import { Routes } from '@angular/router';
import { GuardGuard } from '../guard.guard';

import { AlojamientoComponent } from './Alojamiento/Alojamiento.component';
import { HotelComponent } from './Hotel/Hotel.component';
import { CoWorkingComponent } from './CoWorking/CoWorking.component';

export const VistaServicioRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'Alojamiento/:IdServicio',
        component: AlojamientoComponent
      },
      {
        path: 'CoWorking/:IdServicio',
        component: CoWorkingComponent
      },
      {
        path: 'Hotel/:IdServicio',
        component: HotelComponent
      }
    ]
  }
];
