import { Component, Input, ViewChild, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';

import { SearchServiceService } from 'src/app/services/Search-service.service';

import { DetailInfoCoWorking } from '../../modelos/Search/DetailInfoCoWorking.model';
import { DetailInfoAdditionals } from '../../modelos/Search/DetailInfoAdditionals.model';
import { DetailInfoMultimedia } from '../../modelos/Search/DetailInfoMultimedia.model';
import { DetailInfoSchedules } from '../../modelos/Search/DetailInfoSchedules.model';
import { DetailInfoService } from '../../modelos/Search/DetailInfoService.model';
import { DetailInfoTransports } from '../../modelos/Search/DetailInfoTransports.model';
import { ServiceFavorite } from '../../modelos/servicefavorite.model';
import { AgregarfavoritosComponent } from 'src/app/utilidades/agregarfavoritos/agregarfavoritos.component';
import { GenericStorageService } from 'src/app/services/genericstorage.service';
import { DetailInfoBenefit } from '../../modelos/Search/DetailInfoBenefit.model';
import { ReservationList } from 'src/app/modelos/Reservas/ReservationList.model';
import { Reservation } from 'src/app/modelos/Reservas/Reservation.model';
import { ReservasNotificacionComponent } from 'src/app/utilidades/ReservasNotificacion/ReservasNotificacion.component';

//import { DetailInfoRooms } from '../../modelos/Search/DetailInfoRooms.model';
//import { DetailInfoSpaceBenefit } from '../../modelos/Search/DetailInfoSpaceBenefit.model';
//import { DetailInfoContracts } from '../../modelos/Search/DetailInfoContracts.model';
//import { ServiceSpaceMultimedia } from 'src/app/modelos/servicespacemultimedia.model';


@Component({
  templateUrl: './CoWorking.component.html',
  styleUrls: ['./CoWorking.component.css'],
  providers: [NgbCarouselConfig]
})
export class CoWorkingComponent implements OnInit 
{
  IdServicio: string;
  isSpinnerVisible: boolean = true;
  DefaultImageUrl: string;
  Titulo: string;

  favorito : ServiceFavorite;
  @ViewChild(AgregarfavoritosComponent) compCalificacion: AgregarfavoritosComponent;
  @ViewChild(ReservasNotificacionComponent) compReservaNotif: ReservasNotificacionComponent;

  latA = -34.754764;
  lngA = 149.736246;
  zoom = 10;
  datosRate:  any = {};
  @Input() detailInfoService : DetailInfoService;
  @Input() detailInfoCoWorking : DetailInfoCoWorking[] = [];
  @Input() detailInfoAdditionals : DetailInfoAdditionals[] = [];
  @Input() detailInfoSchedules : DetailInfoSchedules[] = [];
  @Input() detailInfoTransports : DetailInfoTransports[] = [];
  @Input() detailInfoBenefit : DetailInfoBenefit[] = [];
  @Input() detailInfoMultimedia : DetailInfoMultimedia[] = [];
  
  //@Input() detailInfoBenefit : DetailInfoBenefit[] = [];
  //@Input() detailInfoContracts : DetailInfoContracts[] = [];
  //@Input() detailInfoMultimedia : DetailInfoMultimedia[] = [];

  constructor(
    private rutaActiva: ActivatedRoute,
    private serviceSeach: SearchServiceService,
    private servicePublicaciones: SearchServiceService,
    private genStorageFavoritos : GenericStorageService<ServiceFavorite[]>,
    private genStorageServ : GenericStorageService<ReservationList[]>,
    config: NgbCarouselConfig
  ) 
  {
    config.interval = 3000;
    config.wrap = true;
    config.keyboard = true;
    config.showNavigationArrows = true;
    config.showNavigationIndicators = true;

    this.IdServicio = this.rutaActiva.snapshot.params.IdServicio;
    this.DefaultImageUrl = "assets/images/background/SinImagen.png";
    this.CargarInformacionHotel();
  }

  ngOnInit() 
  {
    this.favorito = new ServiceFavorite();
    this.favorito.IdService = this.IdServicio;
    this.favorito.TypeService = "CoWorking";
    this.compCalificacion = new AgregarfavoritosComponent(this.servicePublicaciones, this.genStorageFavoritos);
    //this.compCalificacion.ValidarEstadoFavorito();
  }

  private CargarInformacionHotel()
  {
      this.isSpinnerVisible = true;
      this.datosRate = { nStart: 4, maxStar:  5, HideText: true, IsSimpleStyle: true, InfoAdditional: "186"};

      this.serviceSeach.GetDetailInfo<DetailInfoService>("DetailInfoService", this.IdServicio).subscribe(
          respuesta => 
          { 
            this.detailInfoService = respuesta.JSonResult; 
            this.Titulo = this.detailInfoService.Title;
            this.latA = parseFloat(this.detailInfoService.latitude);
            this.lngA = parseFloat(this.detailInfoService.longitude);

          }, error=> { }
      );

      this.serviceSeach.GetDetailsInfo<DetailInfoCoWorking>("DetailInfoCoWorking", this.IdServicio).subscribe(
        respuesta => 
        { 
          this.detailInfoCoWorking = respuesta.JSonResult; 
          this.isSpinnerVisible = false; 
        }, error=> { });

      this.serviceSeach.GetDetailsInfo<DetailInfoAdditionals>("DetailInfoAdditionals", this.IdServicio).subscribe(
        respuesta => { this.detailInfoAdditionals = respuesta.JSonResult;  }, error=> { });

      this.serviceSeach.GetDetailsInfo<DetailInfoSchedules>("DetailInfoSchedules", this.IdServicio).subscribe(
        respuesta => { this.detailInfoSchedules = respuesta.JSonResult;  }, error=> { });

      this.serviceSeach.GetDetailsInfo<DetailInfoTransports>("DetailInfoTransports", this.IdServicio).subscribe(
        respuesta => { this.detailInfoTransports = respuesta.JSonResult;  }, error=> { });

      this.serviceSeach.GetDetailsInfo<DetailInfoMultimedia>("DetailInfoMultimedia", this.IdServicio, "GetDetailInfoMultimedia_CoWorking").subscribe(
        respuesta => 
        {  
            this.detailInfoMultimedia = respuesta.JSonResult; 
            
            if(this.detailInfoMultimedia == null || this.detailInfoMultimedia.length <= 0)
            {
              var imageDefault: DetailInfoMultimedia = new  DetailInfoMultimedia();
              imageDefault.Content = this.DefaultImageUrl;
              
              this.detailInfoMultimedia = [];
              this.detailInfoMultimedia.push(imageDefault);
            }
            else
            {
              this.detailInfoMultimedia.forEach(imagen => {
                imagen.Content = "http://bricks4us.azurewebsites.net/" + imagen.Content.replace("thumbnail", "mediana");
              });
            }

        }, error=> { });

      this.serviceSeach.GetDetailsInfo<DetailInfoBenefit>("DetailInfoBenefit", this.IdServicio).subscribe(
          respuesta => { this.detailInfoBenefit = respuesta.JSonResult;  }, error=> { });
  }

  private timerFromModel(value: string): string {
    if (!value) {
      return null;
    }
    const split = value.split('T')[1];
    return split.split(':')[0] + ":" + split.split(':')[1];
  }

  private Continuar(IdServiceType: string, IdService: string, IdCoWorkingSpace: string)
  {
      console.log("Registrando en Reservas: " + IdServiceType + " " + IdService + " " + IdCoWorkingSpace);
      
      this.compReservaNotif.Agregar(IdServiceType, IdService, IdCoWorkingSpace, this.Titulo, this.detailInfoService.ServiceType);
  }
}
